import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { logout, checkImpersonation } from 'Shared/ducks/user'
import { mergeProps } from 'Shared/helpers/redux'
import { fetchEmployer } from 'Shared/ducks/user/employer'
import App from 'Employer/components/App'
import { hasOrganisationFetched } from 'Shared/selectors/organisations'

const mapStateToProps = state => ({
  user: state.user,
  hasOrganisationFetched: hasOrganisationFetched(state)
})

const mapDispatchToProps = dispatch => {
  return {
    getInitialData: () => dispatch(fetchEmployer()),
    checkImpersonation: () => dispatch(checkImpersonation()),
    logout: () => dispatch(logout())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(App))



