import React, { PureComponent } from 'react'
import { Route, Switch, Redirect } from 'react-router-dom'
import PropTypes from 'prop-types'
import appRegistry from 'Shared/services/AppRegistry'
import withTranslations from 'Shared/hoc/withTranslations'
import { CandidateProfileViewLoader, JobsViewLoader, JobOffersViewLoader } from 'Shared/components/Main/MainLoaders'
import EmployerPrivateRoute from 'Employer/components/EmployerPrivateRoute'
import { EmployerPermissions } from 'Shared/constants/User'
import loadable from 'Shared/helpers/loadable'
import EmployerAppLoader from './EmployerAppLoader'

const CandidateDetailsViewConnector = loadable(() => import(/*  webpackChunkName: "CandidateDetailsViewConnector" */ 'Employer/connectors/Views/Candidates/CandidateDetailsViewConnector'), CandidateProfileViewLoader)
const CandidatesViewConnector = loadable(() => import(/*  webpackChunkName: "CandidatesViewConnector" */ 'Employer/connectors/Views/Candidates/CandidatesViewConnector'), JobsViewLoader)
const JobOffersViewConnector = loadable(() => import(/*  webpackChunkName: "JobOffersViewConnector" */ 'Employer/connectors/Views/JobOffers/JobOffersViewConnector'), JobOffersViewLoader)
const JobOfferCandidatesConnector = loadable(() => import(/*  webpackChunkName: "JobOfferCandidatesConnector" */ 'Employer/connectors/Views/JobOfferCandidates/JobOfferCandidatesConnector'), JobsViewLoader)
const EditCompanyDetailsViewConnector = loadable(() => import(/*  webpackChunkName: "EditCompanyDetailsViewConnector" */ 'Employer/connectors/Views/Company/EditCompanyDetailsViewConnector'))
const EditJobOffersViewConnector = loadable(() => import(/*  webpackChunkName: "EditJobOffersViewConnector" */ 'Employer/connectors/Views/JobOffers/EditJobOfferViewConnector'))
const SettingsViewConnector = loadable(() => import(/*  webpackChunkName: "EmployerSettingsView" */ 'Employer/connectors/Views/Settings/SettingsViewConnector'))
const ChatAppConnector = loadable(() => import(/*  webpackChunkName: "ChatAppConnector" */ 'Chat/connectors/Components/ChatAppConnector/ChatAppConnector'))

class App extends PureComponent {
  static propTypes = {
    logout: PropTypes.func.isRequired,
    user: PropTypes.object.isRequired
  }

  constructor () {
    super(...arguments)

    this.state = {
      ready: false
    }
  }

  async componentDidMount () {
    try {
      this.setState({ ready: true })
    } catch (error) {
      appRegistry.logger.error(error)

      // this.props.logout()
    }
  }

  getCandidatesViewConnector = props => <CandidatesViewConnector {...props} employerId={this.props.user.details.id} />

  getJobOfferCandidatesConnector = props => <JobOfferCandidatesConnector {...props} employerId={this.props.user.details.id} />

  render () {
    return (this.state.ready && this.props.hasOrganisationFetched)
      ? (
        <Switch>
          <Route path='/employer/chat' component={ChatAppConnector} />
          <Route path='/employer/job-offers' component={JobOffersViewConnector} />
          <Route path='/employer/job-offer/:jobOfferId/candidates/:savedSearchId?' component={this.getJobOfferCandidatesConnector} />
          <Route path='/employer/job-offer/:jobOfferId' component={EditJobOffersViewConnector} />
          <Route path='/employer/candidates/:savedSearchId?' render={this.getCandidatesViewConnector} />
          <Route path='/employer/candidate/:candidateId' component={CandidateDetailsViewConnector} />
          <EmployerPrivateRoute allowedFor={[EmployerPermissions.companyProfile]} path='/employer/company-profile' component={EditCompanyDetailsViewConnector} />
          <Route path='/employer/settings' component={SettingsViewConnector} />
          <Redirect to='/not-found' />
        </Switch>
      )
      : <EmployerAppLoader />
  }
}

export default withTranslations(App)



