import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import Loader from 'Shared/components/Loader/Loader'
import {
  JobOffersViewLoader,
  JobsViewLoader,
  CandidateProfileViewLoader
} from 'Shared/components/Main/MainLoaders'

const EmoployersAppLoader = (props) => {
  const renderLoader = () => <Loader className='loader--app' description={`${props.t('common.loading')}...`} />

  return (
    <Switch>
      <Route path='/employer/chat' component={renderLoader} />
      <Route path='/employer/job-offers' component={JobOffersViewLoader} />
      <Route path='/employer/job-offer/:jobOfferId/candidates/:savedSearchId?' component={JobsViewLoader} />
      <Route path='/employer/job-offer/:jobOfferId' component={renderLoader} />
      <Route path='/employer/candidates' component={JobsViewLoader} />
      <Route path='/employer/candidate/:candidateId' component={CandidateProfileViewLoader} />
      <Route path='/employer/company-profile' component={renderLoader} />
      <Route path='/employer/settings' component={renderLoader} />
      <Redirect to='/not-found' />
    </Switch>
  )
}

export default withTranslations(EmoployersAppLoader)



